.most__item {
  background-color: var(--color-dark);
  border-radius: calc(var(--border-radius-primary) / 2);
  padding: calc(var(--padding-primary) / 3);
}

.most__card_img {
  width: 100%;
  border-radius: calc(var(--border-radius-primary) / 4);
  margin-bottom: calc(var(--margin-primary) / 5);

}

.most__card_content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 0.8rem;
  line-height: 1;
}

.most__card_title {
  font-size: 0.8rem;
}

.most__card_content li span {
  margin-left: 0.5rem;
}