.main-container{
    padding: var(--padding-primary);
    background-color: var(--color-dark);
    border-radius: var(--border-radius-primary);
    color: var(--color-light);
}

@media (max-width : 768px) {
    .main-container{
        padding: calc( var(--padding-primary) / 2);
        background-color: var(--color-dark);
        border-radius: var(--border-radius-primary);
        color: var(--color-light);
    }
    
  }