@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap');
:root{
    --color-dark : #27292a;
    --color-darkest : #1f2122;
    --color-light : #fff;
    --color-primary : #ec6090;
    --color-yellow : yellow;

    --color-bg-body : var(--color-darkest);

    --space-primary : 50px;
    --padding-primary : var(--space-primary);
    --margin-primary : var(--space-primary);

    --border-radius-primary: calc(var(--space-primary) / 2) ;

    --font-primary : 'Poppins', sans-serif ; 
    --font-weight-blod : 700 ;
    --font-weight-normal : 400 ;
  


}
body{
    background-color: var(--color-bg-body);

    font-family: var(--font-primary);
}

ul {
    list-style: none;
}
a{
    display: inline-block;
    color: inherit;
    text-decoration: none;
    text-transform: capitalize;
}