.gaming-library-Card {
  width: 100%;
  background-color: var(--color-dark);
  border-radius: calc(var(--border-radius-primary) / 2);
  padding: calc(var(--padding-primary) / 4) calc(var(--padding-primary) / 2);
  border-bottom: 1px solid var(--color-primary);
  font-size: 0.8rem;

}
.gaming-library-Card ul {
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 1rem;
  flex-wrap: wrap;

}
.gaming-library-Card h4 {
  font-size: 1rem;
}
