.hero-main {
    color: var(--color-light);
    border-radius: var(--border-radius-primary);
    padding: var(--padding-primary);
    margin-block: 20px;
    background: var(--color-dark) url(../../assets/images/banner-bg.jpg) no-repeat center / cover;
}

.hero-text {
    max-width: 500px;

}

.hero-text em {
    font-style: normal;
    color: var(--color-primary);
}

.hero-subtitle {
    font-size: 22px;
    font-weight: var(--font-weight-normal);
    margin-bottom: calc(var(--margin-primary) / 2);
    letter-spacing: 1.5px;
}

.hero-title {
    font-size: 45px;
    font-weight: var(--font-weight-blod);
    text-transform: uppercase;
    margin-bottom: calc(var(--margin-primary) / 2);
}
